.cal form {
  width: 300px;
  padding-top: 15px;
  padding-bottom: 15px;
  text-align: center;
  border: #000 solid 1px;
  border-radius: 25px;
  margin: auto;
  background-color: #bcbec2;
  padding-left: 20px;
  padding-right: 20px;

}

.cal p {
  font-weight: bolder;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.cal input {
  width: 50px;
  height: 50px;
  margin: 5px;
  text-align: center;
  font-size: 20px;
}

.cal input[type="text"] {
  width: 230px;
  height: 50px;
  margin-bottom: 10px;
  border: 1 #000;
  border-radius: 20px;

}

.cal input[type="text"]:focus {
  font-size: 20px;
  background-color: rgba(164, 163, 159, 0.678);
}

.cal input[type="button"] {
  background-color: #8ab1bb;
  cursor: pointer;
  width: 140px;
  height: max-content;
  padding: 5px;
  border-radius: 20px;
}

.cal input[type="text"]:disabled {
  background-color: #ffffff;
}

.cal input[class="reload"] {
  background-color: rgb(245, 175, 24);
  width: 220px;
}